let value1 = "60";
let value2 = "-15";
let value3 = "30";
let value4 = "85";
let value5 = "132";
const MoRaBtn = styled.a`
    width:calc(${window.innerWidth}px * 7 / 10 * 1 / 4 + 280px);
    height:calc((${window.innerHeight}px - 100px) * 7 / 10 - 250px);
    display: inline-block;
    position: absolute;
    left:10%;
    z-index:${props => props.center ? "3" :
        props.x === value2 ? "2" :
            props.x === value5 ? "2" : "1"
    };
    transform:${props => props.x === value2 ? `translate(${props.x}%,20%) scale(0.8) rotate3d(0, 30, 0,50deg)` :
        props.x === value5 ? `translate(${props.x}%,20%) scale(0.8) rotate3d(0, 30, 0,50deg)` :
            props.x === value1 ? `translate(${props.x}%,50%) scale(1)` :
                props.x === value3 ? `translate(${props.x}%,2%) scale(0.5) rotate3d(0, 20, 0, 50deg)` : `translate(${props.x}%,2%) scale(0.5) rotate3d(0, 20, 0, 50deg)`
    };
    opacity: ${props => props.center ? 1 :
        props.x === value2 ? 0.8 :
            props.x === value5 ? 0.8 : 0.5
    };
    box-shadow:${props => props.center ? "2px 4px 3px rgba(20,20,20,0.7)" :
        "0px 4px 3px rgba(20,20,20,0.7)"
    };
    background-image: url(${props => props.assets});
    background-size:cover;
    border-radius: 20px;
    transition:0.5s all;
    &&:hover{
        transform:${props => props.x === value2 ? `translate(${props.x}%,20%) scale(0.82) rotate3d(0, 30, 0,50deg)` :
        props.x === value5 ? `translate(${props.x}%,20%) scale(0.82) rotate3d(0, 30, 0,50deg)` :
            props.x === value1 ? `translate(${props.x}%,50%) scale(1.02)` :
                props.x === value3 ? `translate(${props.x}%,-15%) scale(0.52) rotate3d(0, 20, 0, 50deg)` : `translate(${props.x}%,-15%) scale(0.52) rotate3d(0, 20, 0, 50deg)`
    };
        opacity:1;
    }
`;

